(function() {
  app.behaviors.recordPage = {
    desiredRecordType: 'NOT DEFINED',
    properties: {
      record: {
        type: Object,
        notify: true,
        value: null
      },
      patient: {
        type: Object,
        notify: true,
        value: null
      },
      hasLocalChanges: {
        type: Boolean,
        value: true
      }
    },
    genericNavigatedIn: function(desiredRecordType) {
      var params;
      this.desiredRecordType = desiredRecordType;
      params = this.domHost.getPageParams();
      if (params['record']) {
        if (params['record'] === 'new') {
          this._makeNewRecord();
        } else {
          this._loadRecord(params['record']);
        }
      } else {
        this._notifyInvalidRecord();
      }
      if (this.record) {
        if (params['patient']) {
          return this._loadPatient(params['patient']);
        } else if (this.record.patientSerial) {
          return this._loadPatient(this.record.patientSerial);
        } else {
          return this._notifyInvalidPatient();
        }
      }
    },
    navigatedOut: function() {
      this.record = null;
      return this.patient = null;
    },
    _saveRecord: function() {
      app.db.upsert(this.desiredRecordType, this.record, (function(_this) {
        return function(arg) {
          var serial;
          serial = arg.serial;
          return _this.record.serial === serial;
        };
      })(this));
      return this.domHost.showToast('Record Saved');
    },
    _goBack: function() {
      return this.domHost.navigateToPreviousPage();
    },
    arrowBackButtonPressed: function(e) {
      if (this.hasLocalChanges) {
        return this.domHost.showModalPrompt('Are you sure? All unsaved changes will be lost.', (function(_this) {
          return function(answer) {
            if (answer) {
              return _this._goBack();
            }
          };
        })(this));
      } else {
        return this._goBack();
      }
    },
    saveButtonPressed: function(e) {
      this._saveRecord();
      return this.hasLocalChanges = false;
    },
    _loadPatient: function(patientIdentifier) {
      var list;
      list = app.db.find('patient-list', function(arg) {
        var serial;
        serial = arg.serial;
        return serial === patientIdentifier;
      });
      if (list.length === 1) {
        return this.patient = list[0];
      } else {
        return this._notifyInvalidPatient();
      }
    },
    _notifyInvalidPatient: function() {
      return this.domHost.showModalDialog('Invalid Patient Provided');
    },
    _loadRecord: function(recordIdentifier) {
      var list;
      list = app.db.find(this.desiredRecordType, function(arg) {
        var serial;
        serial = arg.serial;
        return serial === recordIdentifier;
      });
      if (list.length === 1) {
        return this.record = list[0];
      } else {
        return this._notifyInvalidRecord();
      }
    },
    _notifyInvalidRecord: function() {
      return this.domHost.showModalDialog('Invalid Record Provided');
    }
  };

}).call(this);
